@import '../../mixins/mixin.scss';

.slogan_banner{
    z-index: 1;
    color: #fff;
    position: relative;
    top: 20vh;
    overflow-x: hidden;
    -webkit-overflow-x: hidden !important;
    
    .span_slogan{
      margin-left: 8px;
      font-size: 2.7em;
   }
    
  }
  
  .buttons{
    position: relative;
    @include flex(flex-start,center);
    z-index: 1;
    padding-top: 24vh;
    gap: 5vw;
    margin-left: 8px;
  }
  
  @media (max-width: 780px){
    .span_slogan{
      font-size: 2.5em;
    }
  }
  

@media (max-width: 500px){
  .buttons{
    gap: 10vw;
    padding-top: 22vh;
    margin-left: 15%;
    font-size: 1.3em;
  }

}

@media (max-width: 450px){

  .slogan_banner{
    margin-top: 23vh;
  }
  .span_slogan{
    font-size: 1.4em !important;  
  }
}


@media (min-width: 1900px){
  .span_slogan{
    font-size: 3.5em;
  }
}
